body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Noto Sans', Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji';
  font-size: 14px;
}
.app-container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: #f5f5f5;
}
.app-content {
  height: 100vh;
  overflow: hidden;
}
.app-grid-layout {
  height: 100%;
  display: flex;
}
.app-grid-layout .ant-row {
  flex: 1;
  height: 100%;
  display: flex;
  flex-wrap: nowrap;
  width: 100%;
}
.app-panel {
  height: 100%;
  background-color: #fff;
  border-radius: 0;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
  transition: box-shadow 0.3s;
  overflow: hidden;
}
.app-panel:hover {
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.09);
}
.app-panel.left-panel {
  overflow: hidden;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.app-panel.right-panel {
  border-radius: 0;
  flex: 1;
  overflow: hidden;
  box-shadow: -4px 0px 20px 0px #0000000a;
}
.panel-content {
  height: 100%;
  overflow: auto;
  display: flex;
  flex-direction: column;
}
.panel-content.left-panel-content {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;
}
.panel-content.right-panel-content {
  border-radius: 0;
  padding: 12px 24px 24px 24px;
}
.panel-content h2 {
  color: #000;
  font-size: 18px;
  margin-top: 16px;
  margin-bottom: 12px;
}
.panel-content canvas {
  max-width: 100%;
  margin-top: 16px;
  border: 1px solid #f0f0f0;
  border-radius: 4px;
}
.command-form {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;
}
.command-form .form-content {
  display: flex;
  flex-direction: column;
  height: 100%;
  gap: 24px;
}
.command-form .command-input-wrapper {
  margin-top: 8px;
}
.result-container {
  flex: 1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 0;
  position: relative;
  height: 100%;
}
.resize-handle {
  width: 2px;
  background-color: #f0f0f0;
  transition: background-color 0.2s;
}
.resize-handle:hover {
  background-color: #1677ff;
}
.panel-resize-handle {
  background-color: #f0f0f0;
  transition: background-color 0.2s;
  position: relative;
}
.panel-resize-handle.horizontal {
  width: 1px;
  cursor: col-resize;
}
.panel-resize-handle.vertical {
  height: 1px;
  cursor: row-resize;
}
.panel-resize-handle:hover {
  background-color: #1677ff;
}
.panel-resize-handle:active {
  background-color: #1677ff;
}
.panel-resize-handle[data-resize-handle-active] {
  background-color: #1677ff;
}
