.device-header {
  display: flex;
  align-items: center;
  margin-bottom: 16px;
}
.device-header .device-title-container {
  display: flex;
  align-items: center;
}
.device-header .device-title {
  margin: 0;
  font-size: 18px;
  color: #000;
  margin-right: 12px;
  display: flex;
  align-items: center;
  height: 32px;
}
.device-info-display {
  display: flex;
  align-items: center;
  padding: 4px 12px 4px 8px;
  background: #f0f0f0;
  border-radius: 20px;
  height: 32px;
}
.device-info-display .device-icon-container {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background: #3b82f6;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.device-info-display .device-icon-container .device-icon {
  font-size: 14px;
  color: white;
}
.device-info-display .device-icon-container .status-indicator {
  position: absolute;
  right: -5px;
  bottom: -5px;
  display: flex;
  align-items: center;
}
.device-info-display .device-name {
  font-weight: bold;
  font-size: 16px;
  color: #333;
  max-width: 200px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin-left: 8px;
}
.device-info-display .device-name.no-device {
  color: #999;
  font-weight: normal;
  font-style: italic;
}
.device-dropdown {
  width: 430px;
  background: #fff;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0px 10px 20px 0px #00000005;
  border: 1px solid #EAEDF1;
}
.device-dropdown .dropdown-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 16px 12px;
}
.device-dropdown .dropdown-header .dropdown-title {
  font-weight: bold;
  font-size: 16px;
  color: #333;
}
.device-dropdown .device-list-item {
  padding: 5px 17px 9px 6px;
  cursor: pointer;
}
.device-dropdown .device-list-item.selected {
  background: #2B83FF14;
}
.device-dropdown .device-list-item.offline {
  cursor: not-allowed;
  opacity: 0.5;
}
.device-dropdown .device-list-item .device-item-content {
  display: flex;
  align-items: center;
}
.device-dropdown .device-list-item .device-item-content .device-item-icon-container {
  width: 34px;
  height: 34px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 12px;
}
.device-dropdown .device-list-item .device-item-content .device-item-icon-container .device-item-icon {
  font-size: 22px;
  color: #666;
}
.device-dropdown .device-list-item .device-item-content .device-item-info {
  flex: 1;
}
.device-dropdown .device-list-item .device-item-content .device-item-info .device-item-name {
  font-weight: bold;
  font-size: 15px;
  color: #333;
}
.device-dropdown .device-list-item .device-item-content .device-item-info .device-item-status {
  display: flex;
  align-items: center;
  margin-top: 4px;
}
.device-dropdown .device-list-item .device-item-content .device-item-info .device-item-status .status-badge {
  display: flex;
  align-items: center;
  margin-right: 12px;
}
.device-dropdown .device-list-item .device-item-content .device-item-info .device-item-status .status-badge .status-text {
  color: #666;
  font-size: 12px;
}
.device-dropdown .device-list-item .device-item-content .device-item-info .device-item-status .device-id-container {
  color: #999;
  font-size: 12px;
}
.device-dropdown .device-list-item .device-item-content .current-device-indicator {
  margin-left: auto;
  color: #1890ff;
  font-weight: bold;
  font-size: 13px;
}
.device-dropdown .device-list-empty {
  padding: 20px;
  text-align: center;
  color: #999;
}
.status-dot {
  margin-right: 4px;
  font-size: 12px;
}
.status-divider {
  margin: 0 4px;
}
